import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/config/api.config';
import { CreateEnderecoDTO } from '../../interfaces/enderecos/CreateEnderecoDTO';
import { CreateUserDTO } from '../../interfaces/users/CreateUserDTO';
import { CreateUserResponseDTO } from '../../interfaces/users/CreateUserResponseDTO';
import { FilterUserDTO } from '../../interfaces/users/FilterUserDTO';
import { UpdateUserDTO } from '../../interfaces/users/UpdateUserDTO';
import { UserDTO } from '../../interfaces/users/UserDTO';

@Injectable({
  providedIn: 'root'
})
export class FuncionarioService {

  constructor(
    private http: HttpClient,
  ) { }

  findById(user_id): Observable<UserDTO> {
    return this.http.get<UserDTO>(
      `${API_CONFIG.baseURL}/funcionario/${user_id}`,
      {
        responseType: 'json',
      });
  }

  listAllLogedUsers(loterica_id): Observable<UserDTO[]> {
    return this.http.get<UserDTO[]>(
      `${API_CONFIG.baseURL}/funcionario/logged/${loterica_id}`,
      {
        responseType: 'json',
      });
  }

  listAllUsers(loterica_id, filter?: FilterUserDTO): Observable<UserDTO[]> {

    const params: any = {};

    if (filter && filter.perfil_nomes) {
      params.perfil_nomes = JSON.stringify(filter.perfil_nomes);
    }


    return this.http.get<UserDTO[]>(
      `${API_CONFIG.baseURL}/funcionario/all/${loterica_id}`,
      {
        responseType: 'json',
        params
      });
  }

  createUser(newUser: CreateUserDTO): Observable<CreateUserResponseDTO> {
    return this.http.post<CreateUserResponseDTO>(
      `${API_CONFIG.baseURL}/funcionario`,
      newUser,
      {
        responseType: 'json',
      });
  }

  updateUser(updatedUser: UpdateUserDTO, idUser: string): Observable<UserDTO> {
    return this.http.put<UserDTO>(
      `${API_CONFIG.baseURL}/funcionario/${idUser}`,
      updatedUser,
      {
        responseType: 'json',
      });
  }

  createEndereco(newEndereco: CreateEnderecoDTO, user_id: string): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/funcionario/${user_id}/endereco`,
      newEndereco,
      {
        responseType: 'json',
      });
  }

  updateEndereco(newEndereco: CreateEnderecoDTO, user_id: string): Observable<any> {
    return this.http.put<any>(
      `${API_CONFIG.baseURL}/funcionario/${user_id}/endereco`,
      newEndereco,
      {
        responseType: 'json',
      });
  }


  uploadAvatar(anexo: File): Observable<any> {
    const formData = new FormData();

    formData.append('avatar', anexo);

    return this.http.patch<any>(`${API_CONFIG.baseURL}/funcionario/avatar`, formData);
  }
}
