import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { Subscription } from 'rxjs';

import { ConfirmationService, MessageService } from 'primeng/api';
import { StatusTransacaoEnum } from 'src/app/shared/enumerations/statusTransacao.enum';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { FiltroTransacao } from 'src/app/shared/interfaces/transacoes/FiltroTransacao';
import { TransacaoDTO } from 'src/app/shared/interfaces/transacoes/TransacaoDTO';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { TransacaoService } from 'src/app/shared/services/transacao/transacao.service';
import { externalLinkHandle } from 'src/app/shared/utils/ExternalLink';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { TipoTransacaoDTO } from 'src/app/shared/interfaces/transacoes/tipo/TipoTransacaoDTO';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';

@Component({
  selector: 'app-transacoes',
  templateUrl: './transacoes.page.html',
  styleUrls: ['./transacoes.page.scss'],
})
export class TransacoesPage implements OnInit, OnDestroy {

  transacoes: TransacaoDTO[] = [];
  transacoesOriginais: TransacaoDTO[] = [];

  isLoaded = false;

  showJustificativaModal = false;

  transacaoToBeDeleted: TransacaoDTO;

  sortTipoTransacao: TipoTransacaoDTO[] = [];
  selectedTipoTransacao: TipoTransacaoDTO;

  sortUserInicial: UserDTO[] = [];
  selectedUserInicial: UserDTO;

  private subscriptions = new Subscription();

  constructor(
    private transacaoService: TransacaoService,
    private confirmationService: ConfirmationService,
    private modal: ModalController,
    private toastService: MessageService,
    private storage: StorageService,
    private funcionarioService: FuncionarioService,
  ) { }

  ngOnInit() {
    const { id: user_final_id, loterica_id } = this.storage.getLocalUser().user
    this.loadTransacoes({
      user_final_id,
      status_transacao_cod: StatusTransacaoEnum.PENDENTE,
      loterica_id
    });
    this.loadFuncionarios();
    this.loadTipoTransacao();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadTransacoes({ loterica_id, status_transacao_cod, tipo_transacao_id, user_final_id, user_inicial_id, }: FiltroTransacao) {
    //const loterica_id = "c8af6707-748d-4997-b8b3-84feaffc309a";
    const sub = this.transacaoService.list({ loterica_id, status_transacao_cod, tipo_transacao_id, user_final_id, user_inicial_id, })
      .subscribe(data => {
        this.transacoes = data;
        this.transacoesOriginais = data;
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  loadTipoTransacao() {
    const sub = this.transacaoService.listTipoTransacao()
      .subscribe(data => {
        this.sortTipoTransacao = data;
      });

    this.subscriptions.add(sub);
  }

  loadFuncionarios() {
    const { loterica_id } = this.storage.getLocalUser().user;

    const sub = this.funcionarioService.listAllUsers(loterica_id)
      .subscribe(data => {
        this.sortUserInicial = data;
      });

    this.subscriptions.add(sub);
  }

  downloadAnexo(transacao: TransacaoDTO) {
    this.toastService.clear();
    if (transacao.anexo) {
      externalLinkHandle(transacao.anexo);
    } else {
      this.presentToast("warn", "Sem anexo", "A transação não possui anexo", Toast.mediumDuration);
    }
  }

  aprovar(transacao: TransacaoDTO) {
    const { id } = transacao;
    this.confirmationDialog(StatusTransacaoEnum.APROVADO, id);
  }

  rejeitar(transacao: TransacaoDTO) {
    this.showJustificativaModal = true;
    this.transacaoToBeDeleted = transacao;
  }

  atualizar() {
    const { id: user_final_id, loterica_id } = this.storage.getLocalUser().user
    this.loadTransacoes({
      user_final_id,
      status_transacao_cod: StatusTransacaoEnum.PENDENTE,
      loterica_id
    });
  }

  filterTipoTransacao(event?) {
    if (event) {
      event.first = 0;
    }

    if (this.selectedUserInicial) {
      this.transacoes = this.transacoesOriginais.filter(t => t.user_inicial.id == this.selectedUserInicial.id);
      this.transacoes = this.transacoes.filter(t => t.tipo_transacao.codigo == this.selectedTipoTransacao.codigo);
    } else {
      this.transacoes = this.transacoesOriginais.filter(t => t.tipo_transacao.codigo == this.selectedTipoTransacao.codigo);
    }
  }

  filterSolicitante(event?) {
    if (event) {
      event.first = 0;
    }

    if (this.selectedTipoTransacao) {
      this.transacoes = this.transacoesOriginais.filter(t => t.tipo_transacao.codigo == this.selectedTipoTransacao.codigo);
      this.transacoes = this.transacoes.filter(t => t.user_inicial.id == this.selectedUserInicial.id);
    } else {
      this.transacoes = this.transacoesOriginais.filter(t => t.user_inicial.id == this.selectedUserInicial.id);
    }
  }

  filterClear() {
    this.transacoes = this.transacoesOriginais;

    if (this.selectedUserInicial) {
      this.transacoes = this.transacoesOriginais.filter(t => t.user_inicial.id == this.selectedUserInicial.id);
    }

    if (this.selectedTipoTransacao) {
      this.transacoes = this.transacoesOriginais.filter(t => t.tipo_transacao.codigo == this.selectedTipoTransacao.codigo);
    }
  }

  confirmationDialog(status: StatusTransacaoEnum, transacao_id: string) {
    const operacao = status == 1 ? 'Rejeitada' : 'Aprovada';
    this.confirmationService.confirm({
      message: `Deseja mesmo que esta transação seja ${operacao}?`,
      header: 'Confirmação',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const sub = this.transacaoService.changeStatus({ status, transacao_id })
          .subscribe(data => {
            const { id: user_final_id, loterica_id } = this.storage.getLocalUser().user
            this.loadTransacoes({
              user_final_id,
              status_transacao_cod: StatusTransacaoEnum.PENDENTE,
              loterica_id
            });

            this.presentToast('success', 'Sucesso!', `${operacao} com sucesso!`, Toast.mediumDuration);
          });

        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastService.clear();
      }
    });
  }

  getNotification(event) {
    console.log(event);
    const { hasToClose, justificativa } = event;

    if (hasToClose) {
      this.showJustificativaModal = false;
    }

    if (justificativa) {
      const { id } = this.transacaoToBeDeleted;
      const sub = this.transacaoService.changeStatus({ status: StatusTransacaoEnum.REJEITADO, transacao_id: id, justificativa, })
        .subscribe(data => {
          const { id: user_final_id, loterica_id, } = this.storage.getLocalUser().user
          this.loadTransacoes({
            user_final_id,
            status_transacao_cod: StatusTransacaoEnum.PENDENTE,
            loterica_id,
          });
          this.transacaoToBeDeleted = undefined;
          this.presentToast('success', 'Sucesso!', `Rejeitado com sucesso!`, Toast.mediumDuration);
        });

      this.subscriptions.add(sub);
    }
    this.transacaoToBeDeleted = undefined;
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      if (hasUpdate) {
        this.isLoaded = false;
        const { loterica_id } = this.storage.getLocalUser().user
        this.loadTransacoes({ loterica_id });
        this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
      }
    });

    return await modal.present();
  }



  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}

